import React from "react";
import { Wrapper, Title, LogoList, LogoWrapper, Logo } from "./elements";

interface Props {
  logos: Array<string>;
  title: String;
}

const getColumns = () => ({
  numColumns: 3,
  numColumnsBreakpointMedium: 3,
  numColumnsBreakpointSmall: 1,
});

const TrustedByStyled = ({ logos, title }: Props) => (
  <Wrapper>
    <Title>{title}</Title>
    <LogoList>
      {Object.keys(logos).map((company) => (
        <LogoWrapper key={company} {...getColumns()}>
          <Logo src={logos[company]} />
        </LogoWrapper>
      ))}
    </LogoList>
  </Wrapper>
);

export default TrustedByStyled;
