import React from "react";
import oracle from "assets/img/logos/not-brex/grayscale/dark-text/oracle-netsuite.png";
import quickBooks from "assets/img/logos/not-brex/grayscale/dark-text/quick-books.png";
import xero from "assets/img/logos/not-brex/grayscale/dark-text/xero.png";
import expensify from "assets/img/logos/not-brex/grayscale/dark-text/expensify.png";
import videoPaths from "assets/video";
import {
  StyledDisabledMobileVideo,
  Container,
  LogoWrapper,
  Logo,
  LogoList,
} from "./elements";

const logos = [oracle, quickBooks, xero, expensify];

export const NetSuiteVideo = () => (
  <Container>
    <StyledDisabledMobileVideo
      allowVideoOnSmallScreen
      playsInline
      autoPlay
      loop
      muted
      dataSrc={videoPaths.flyoverVideo}
    />
    <LogoList>
      {Object.keys(logos).map((company) => (
        <LogoWrapper key={company} numColumns={2}>
          <Logo src={logos[company]} />
        </LogoWrapper>
      ))}
    </LogoList>
  </Container>
);
