import React from "react";
import InternalLink from "components/reusable/atoms/links/InternalLink";

import logo from "assets/img/logos/brex/logo-white.svg";

import { Navbar, LogoWrapper, Logo, ApplyButton } from "./elements";

const CustomHeader = ({ executeScroll }) => (
  <Navbar>
    <LogoWrapper>
      <InternalLink to="/">
        <Logo src={logo} alt="Brex" />
      </InternalLink>
    </LogoWrapper>
    {
      // hash links need to use anchor tags because Gatsby
      // doesn't support them well
    }
    <ApplyButton href="#scheduleDemo" onClick={executeScroll}>
      Schedule Demo
    </ApplyButton>
  </Navbar>
);

export default CustomHeader;
